/* === Template === */
html,
body {
    min-width: 320px;
}

body {
    overflow-x: visible;
    position: relative;
    width: 100%;
}

div {
    border: none;
    outline: none;
}

a {
    color: #0366d6;
    cursor: pointer !important;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

audio, embed, img, iframe, object, video {
    height: auto;
    max-width: 100%;
}

fieldset, iframe {
    border: 0;
}

img {
    border: none;
    height: auto;
    vertical-align: top;
    width: auto;
}

ul, li {
    list-style-type: none;
}

input, textarea {
    vertical-align: top;
}

b, strong {
    font-weight: bold;
}

em, i {
    font-style: italic;
    font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: normal;
}



