/* === Reset === */
html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
    -moz-box-sizing: inherit;
    -webkit-box-sizing: inherit;
}

*:focus {
    outline: none;
}

html,
body,
blockquote,
dd,
dl,
dt,
fieldset,
figure,
form,
hr,
h1,
h2,
h3,
h4,
h5,
h6,
iframe,
legend,
li,
ol,
p,
pre,
textarea,
ul {
    margin: 0;
    padding: 0;
}

button,
input,
select {
    margin: 0;
}

td, th {
    padding: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
    display: block;
}



